<template>
  <div class="container">
    <div class="option-wrapper">
      <label for="region">区域:</label>
      <select name="region" id="region" v-model="region">
        <option value="region4163">大连国际会议中心</option>
        <option value="region4164">棒棰岛风景区</option>
      </select>
      <label for="start">开始时间:</label>
      <input type="date" name="start" v-model="start" />
      <label for="end">结束时间:</label>
      <input type="date" name="end" v-model="end" />
      <input type="button" value="查看" @click="handleChange" />
    </div>
    <div class="charts">
      <div class="row">
        <div class="left-chart">
          <LineChart :options="lineoption2020"></LineChart>
        </div>
        <div class="right-chart">
          <LineChart :options="dayOption2020"></LineChart>
        </div>
      </div>
      <div class="row">
        <div class="left-chart">
          <LineChart :options="lineoption2019"></LineChart>
        </div>
        <div class="right-chart">
          <LineChart :options="dayOption2019"></LineChart>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import LineChart from '../components/LineChart'
export default {
  name: 'CityInfo',
  components: {
    LineChart,
  },
  data() {
    return {
      region: 'region4163',
      start: '2020-02-03',
      end: '2020-02-11',
      lineoption2020: {
        title: {
          text: '2020年',
        },
        tooltip: {
          trigger: 'axis',
        },
        legend: {
          data: [],
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: [
            '0点',
            '1点',
            '2点',
            '3点',
            '4点',
            '5点',
            '6点',
            '7点',
            '8点',
            '9点',
            '10点',
            '11点',
            '12点',
            '13点',
            '14点',
            '15点',
            '16点',
            '17点',
            '18点',
            '19点',
            '20点',
            '21点',
            '22点',
            '23点',
          ],
        },
        yAxis: {
          type: 'value',
        },
        series: [],
      },
      lineoption2019: {
        title: {
          text: '2019年',
        },
        tooltip: {
          trigger: 'axis',
        },
        legend: {
          data: [],
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true,
        },
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: [
            '0点',
            '1点',
            '2点',
            '3点',
            '4点',
            '5点',
            '6点',
            '7点',
            '8点',
            '9点',
            '10点',
            '11点',
            '12点',
            '13点',
            '14点',
            '15点',
            '16点',
            '17点',
            '18点',
            '19点',
            '20点',
            '21点',
            '22点',
            '23点',
          ],
        },
        yAxis: {
          type: 'value',
        },
        series: [],
      },
      dayOption2020: {
        title: {
          text: '2020年日流量',
        },
        tooltip: {},
        grid: {
          left: '3%',
          right: '4%',
          bottom: '8%',
          containLabel: true,
        },
        dataZoom: [
          {
            show: true,
            realtime: true,
            start: 20,
            end: 40,
          },
          {
            type: 'inside',
            realtime: true,
            start: 20,
            end: 40,
          },
        ],
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: [],
        },
        yAxis: {
          type: 'value',
        },
        series: [],
      },
      dayOption2019: {
        title: {
          text: '2019年日流量',
        },
        tooltip: {},
        grid: {
          left: '3%',
          right: '4%',
          bottom: '8%',
          containLabel: true,
        },
        dataZoom: [
          {
            show: true,
            realtime: true,
            start: 20,
            end: 40,
          },
          {
            type: 'inside',
            realtime: true,
            start: 20,
            end: 40,
          },
        ],
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: [],
        },
        yAxis: {
          type: 'value',
        },
        series: [],
      },
    }
  },
  created() {},
  mounted() {
    this.getTwoYears()
  },
  methods: {
    handleChange() {
      let that = this
      this.$axios(`data/${this.region}.json`)
        .then((response) => {
          if (response.status === 200) {
            // 更新时初始化
            that.lineoption2020.legend.data = []
            that.lineoption2020.series = []
            const dates = Object.keys(response.data)
            let filter_days = dates.filter((each) => {
              return (
                Date.parse(each) >= Date.parse(`${this.start}`) &&
                Date.parse(each) <= Date.parse(`${this.end}`)
              )
            })
            let filter_data = {}
            filter_days.forEach((day) => {
              filter_data[day] = response.data[day]
            })
            let filter_dates = Object.keys(filter_data)
            let series = Object.values(filter_data)
            that.lineoption2020.legend.data = filter_dates
            for (let i = 0; i < series.length; i++) {
              that.lineoption2020.series.push({
                name: filter_dates[i],
                type: 'line',
                symbol: 'circle',
                smooth: true,
                data: series[i],
              })
            }
          } else {
            console.log(response)
          }
        })
        .catch((err) => console.log(err))
      // 2019年的数据
      this.$axios(`data/pre${this.region}.json`)
        .then((response) => {
          if (response.status === 200) {
            // 更新时初始化
            that.lineoption2019.legend.data = []
            that.lineoption2019.series = []
            const dates = Object.keys(response.data)
            let filter_days = dates.filter((each) => {
              return (
                Date.parse(each) >= Date.parse(`${this.start.replace('2020','2019')}`) &&
                Date.parse(each) <= Date.parse(`${this.end.replace('2020','2019')}`)
              )
            })
            let filter_data = {}
            filter_days.forEach((day) => {
              filter_data[day] = response.data[day]
            })
            let filter_dates = Object.keys(filter_data)
            let series = Object.values(filter_data)
            that.lineoption2019.legend.data = filter_dates
            for (let i = 0; i < series.length; i++) {
              that.lineoption2019.series.push({
                name: filter_dates[i],
                type: 'line',
                symbol: 'circle',
                smooth: true,
                data: series[i],
              })
            }
          } else {
            console.log(response)
          }
        })
        .catch((err) => console.log(err))
      this.getTwoYears()
    },
    getTwoYears() {
      this.$axios(`data/${this.region}.json`)
        .then((response) => {
          if (response.status === 200) {
            this.dayOption2020.series = []
            let data = response.data
            const dates = Object.keys(data)
            let series = Object.values(data)
            this.dayOption2020.xAxis.data = dates
            let newSeries = series.map((s) => {
              return s.reduce((a, b) => a + b, 0).toFixed(2)
            })
            this.dayOption2020.series.push({
              name: '2020年日流量',
              type: 'line',
              data: newSeries,
              markLine: {
                label: {
                  position: 'start',
                },
                data: [{ type: 'average', name: '平均值' }],
              },
            })
          } else {
            console.log(response)
          }
        })
        .catch((err) => console.log(err))
      //  2019年
      this.$axios(`data/pre${this.region}.json`)
        .then((response) => {
          if (response.status === 200) {
            this.dayOption2019.series = []
            let data = response.data
            const dates = Object.keys(data)
            let series = Object.values(data)
            this.dayOption2019.xAxis.data = dates
            let newSeries = series.map((s) => {
              return s.reduce((a, b) => a + b, 0).toFixed(2)
            })
            this.dayOption2019.series.push({
              name: '2019年日流量',
              type: 'line',
              data: newSeries,
              markLine: {
                label: {
                  position: 'start',
                },
                data: [{ type: 'average', name: '平均值' }],
              },
            })
          } else {
            console.log(response)
          }
        })
        .catch((err) => console.log(err))
    },
  },
}
</script>

<style lang="stylus" scoped>
.container
  display flex
  margin-top 20px
  justify-content center
  flex-direction column
  .option-wrapper
    display flex
    justify-content center
    label
      margin-top 10px
      padding 0 8px
    input, select
      outline none
      border 1px solid #333
      padding 2px
      padding-left 6px
      transition 0.3s
    input:last-child
      margin-left 8px
      padding 0 10px
      background #333
      color #fff
    input:hover, select:hover
      box-shadow 0 0 10px rgba(0, 0, 0, 0.3)
  .charts
    display flex
    justify-content center
    flex-direction column
    .row
      display flex
      justify-content center
      .left-chart, .right-chart
        width 70%
        border 1px solid #333
        border-top 4px solid
        margin 20px 15px
        padding 20px
        padding-bottom 30px
        border-radius 5px
        & >>> .wrapper // 样式穿透
          height 500px
          box-shadow none
          margin-top 0
      .right-chart
        width 30%
</style>